
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},

components: {
Alert,
Loading,
Toast,
XHeader,
XInput,
XButton,
Group,
Grid,
GridItem,
PopupPicker,
Flexbox,
FlexboxItem,
Previewer
},

data() {
return {
apiHost: '',
address: '',
lon: '',
lat: '',
city: '',
imgData: '',
isShowImg: false,
pickerVal: [],
pickerList: [],
showSaveState: false,
saveText: '',
alertTitle: '',
alertContent: '',
showAlert: false,
savedData: null
}
},

mounted(){
},

computed: {
showImg(){
return this.$store.state.showUpload;
},
getImgUrl(){
return apiHost + this.$store.state.imgUrl;
},
getRouteId(){
return this.$store.state.routerId;
},
},

methods: {

scanCode(){
client.qr("qrCallBack");
},


uploadImg(){
client.takePhoto("getPic");
},


getGps(){
const gpsStr = client.getGps();
const gps = gpsStr.split("::");
this.lon = gps[0];
this.lat = gps[1];
this.address = gps[2];
this.city = gps[3];
},


showImgDetail(){
this.isShowImg = true;
},


deleteImg(){
this.$store.state.imgUrl = '';
this.$store.state.showUpload = false;
this.isShowImg = false;
},


check(formData){
if(!formData.routerId){
this.showSaveState = true;
this.saveText = 'Id';
return false;
}else if(!formData.addr){
this.showSaveState = true;
this.saveText = '';
return false;
}else if(!formData.img){
this.showSaveState = true;
this.saveText = '';
return false;
}else{
return true;
}
},

save(){








const routerData = {
routerId: this.$store.state.routerId,
lon: this.lon,
lat: this.lat,
addr: this.address,
img: this.$store.state.imgUrl
};

if(!this.check(routerData)){
return;
}

if(routerData === this.savedData){
this.showSaveState = true;
this.saveText = '';
return;
}

this.$http.ajax({
url: apiHost + "router_stat/api/router/report",
dataType: 'json',
type: 'post',
data: {
data: JSON.stringify(routerData)
},
context: this,
}).done(res => {
if (res.result === 1) {
this.showSaveState = true;
this.saveText = '';
this.savedData = routerData;
this.$store.state.routerId = '';
this.$store.state.imgUrl = '';
this.$store.state.showUpload = false;
this.address = '';
} else {
this.alertTitle = "";
this.alertContent = res.msg;
this.showAlert = true;
}
}).fail(res => {
this.alertTitle = "";
this.alertContent = res.responseText;
this.showAlert = true;
})
}
}
}



