
import XHeader from 'vux/src/components/x-header/index.vue'


export default{
components: {
XHeader,
},
data(){
return {
pageSize: 10,
pageNo: 1,
totalRows: null,
installList: [],
allLoaded: false
}
},
created(){
this.getList();
},
methods: {
getList(){
this.$http.ajax({
url: apiHost + "router_stat/api/router/list",
dataType: 'json',
type: 'POST',
data: {
nextpage: JSON.stringify({
pageNo: this.pageNo,
pageSize: this.pageSize
})
},
context: this,
}).done(res => {
if (res.result === 1) {
this.totalRows = res.page.totalRows;
this.pageNo++;
this.installList.push(...res.list);
}
}).fail(res => {

});
},

getImgUrl(item){
return apiHost + item;
},

loadBottom(){
setTimeout(() => {
this.getList();
if(this.installList.length === this.totalRows){
this.allLoaded = true;
}
this.$refs.loadmore.onBottomLoaded();
},2000);
}
}
}



